.footer {
  padding-bottom: 90px;
  overflow-x: hidden;

  @media (max-width: $tablet-max-width) {
    padding-bottom: 53px;
  }

  @media (max-width: $mobile-max-width) {
    padding-bottom: 50px;
  }
}

.footer__wrapper {
  margin: 0 auto;
  width: 957px;

  @media (max-width: $tablet-max-width) {
    width: clamp(698px, 100vw, 1000px);
  }

  @media (max-width: $mobile-max-width) {
    width: clamp(320px, 100vw, 500px);
  }
}
